// login module
import { makeAutoObservable } from 'mobx'
import { http, setToken, getToken, removeToken, saveUser, getUser, removeUser } from '@/utils'
//import app from './server'
import axios from 'axios'


class LoginStore {
  token = getToken() || ''
  constructor() {
    // 響應式
    makeAutoObservable(this)
  }
  login = async ({ id, tel }) => {
    // 調用登入接口
    const res = await axios.post('https://192.168.11.118:7000/signin', {
      id,
      tel
    })
    // 存入 token
    this.token = res.data.token
    // 存入 ls
    setToken(this.token)
  }
  // 退出登入
  logOut = () => {
    this.token = ''
    removeToken()
  }
}

export default LoginStore