import React from 'react'
import './index.scss'
import { Card, Button, Form, Input, message, Space, Divider } from 'antd'
// import 'antd/dist/reset.css'
import logo from '@/assets/logo.png'
import { useNavigate } from 'react-router-dom'
import { useState, useEffect } from 'react'
// 導入樣式文件
import './index.scss'
import { useStore } from '@/store'
import { getUser } from '@/utils/storageUtils'
import axios from 'axios'
import {
  CopyrightOutlined,
} from '@ant-design/icons'

const Register = () => {
  const tel = getUser('user_key')
  const [form] = Form.useForm()
  const getUserInfo = async () => {
    // 調用接口獲取數據
    const res = await axios.get(`https://192.168.11.118:7000/user/profile/${tel}`)
    var userInfo = res.data
    var hei = userInfo.data[0].height
    var wei = userInfo.data[0].weight
    form.setFieldsValue({
      height: hei,
      weight: wei
    })
  }
  useEffect(() => {
    getUserInfo()
    //channelStore.loadChannelList()
  }, [])
  const navigate = useNavigate()
  const onFinish = async (values, e) => {
    const { height, weight } = values
    await axios
      .post(`https://192.168.11.118:7000/signup`, {
        height,
        weight,
        tel
      })
      .then((res) => {
        message.success('輸入成功')
        navigate("/")
      })
  }

  const onFinishFailed = (errorInfo) => {
    const { height, weight } = errorInfo.values
    if (height === "") {
      message.error('請輸入身高!')
    } else if (weight === "") {
      message.error('請輸入體重')
    }
    console.log('Failed:', errorInfo)
  }

  const goBack = (e) => {
    navigate('/login')
  }

  return (
    <div>
      <div className='login'>
        <Card className='login-container'>
          <img className='login-logo' src={logo} alt='' />
          {/* 登入表單 */}
          {/* 子項用到的觸發事件，需要在 Form 中都聲明一下才可以 */}
          <Form
            name="basic"
            form={form}
            onFinish={onFinish}
            onFinishFailed={onFinishFailed}
            autoComplete="off"
          >
            <Form.Item
              name="height"
              label='身高：'
              rules={[
                {
                  required: true,
                  message: '身高不能為空!',
                },
                {
                  pattern: /^[0-9]+$/,
                  message: '身高有非數字字元',
                  validateTrigger: 'onBlur'
                }
              ]}
            >
              <Input size="large" placeholder="請输入身高" />
            </Form.Item>

            <Form.Item
              name="weight"
              label='體重：'
              rules={[
                {
                  required: true,
                  message: '體重不能為空!',
                },
                {
                  pattern: /^[0-9]+$/,
                  message: '體重有非數字字元!',
                  validateTrigger: 'onBlur'
                }
              ]}
            >
              <Input size="large" placeholder="請输入體重" />
            </Form.Item>

            <Form.Item style={{ textAlign: 'center' }}>
              <Space wrap size={30}>
                <Button type="primary" htmlType="submit" size='large' >
                  前往檢測
                </Button>
                <Button type="primary" size="large" onClick={goBack}>
                  返回登入
                </Button>
              </Space>
            </Form.Item>
          </Form>
        </Card>
        <div className='setInfo'>
          <Divider className='dash' style={{ width: '100%', color: '#4cb804', fontWeight: 'bold', fontSize: 18, borderColor: 'white' }}><CopyrightOutlined /> {new Date().getFullYear()}-遠漢科技</Divider>
          <p className='content'>本應用為輔助檢測用途，數據僅供參考</p>
        </div>
      </div>
    </div >
  )
}

export default Register